    private rxBundleCacheService: RxBundleCacheService,
    private rxBundleService: RxBundleService,
    private rxWizardService: RxWizardService,
    private injector: Injector,
    private axBundleDeploymentService: AxBundleDeploymentService,
    private rxModalService: RxModalService,
    private translateService: TranslateService,
    private adaptModalService: AdaptModalService,
    private router: Router,
    private axLocalizedStringsDataPageQuery: AxLocalizedStringsDataPageQuery,
    private ngZone: NgZone,
    private rxLocalizedStringsLoaderService: RxLocalizedStringsLoaderService,
    private rxNotificationService: RxNotificationService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxCreatePackageStatusDataPageService: RxCreatePackageStatusDataPageService,
    private rxOverlayService: RxOverlayService,
    private renderer: Renderer2,
    private rxSessionExpirationService: RxSessionExpirationService
