  uploadTranslations(bundle: BundleDetails): void {
    const unlisten = this.renderer.listen(this.fileInput.nativeElement, 'change', (event: Event) => {
      const element = event.target as HTMLInputElement;
      const file = toArray(element.files)[0];
      const fileReader = new FileReader();
      const translationsToUpload$ = [];
      const successfulLocales = [];
      const failedLocales = [];

      if (file) {
        fileReader.onloadend = () => {
          const content = attempt(JSON.parse.bind(null, fileReader.result));

          Object.keys(content).map((locale) => {
            const translations = content[locale];

            if (!isObject(translations)) {
              this.rxNotificationService.addErrorMessage(
                this.translateService.instant(
                  'com.bmc.arsys.rx.innovation-studio.upload-translations.locale-error.message'
                )
              );
            }

            translationsToUpload$.push(
              this.rxLocalizedStringsLoaderService.uploadTranslation(bundle.id, locale, translations).pipe(
                tap(() => {
                  successfulLocales.push(locale);
                }),
                catchError(() => {
                  failedLocales.push(locale);

                  return of({});
                })
              )
            );
          });

          forkJoin(translationsToUpload$).subscribe(() => {
            if (failedLocales.length > 0) {
              this.rxNotificationService.addErrorMessage(
                this.translateService.instant(
                  'com.bmc.arsys.rx.innovation-studio.upload-translations.partial-failure.message',
                  {
                    locales: failedLocales.join(' ')
                  }
                )
              );
            }

            if (successfulLocales.length === 0) {
              this.rxNotificationService.addErrorMessage(
                this.translateService.instant('com.bmc.arsys.rx.innovation-studio.upload-translations.failure.message')
              );
            }

            if (failedLocales.length === 0) {
              this.rxNotificationService.addSuccessMessage(
                this.translateService.instant('com.bmc.arsys.rx.innovation-studio.upload-translations.success.message')
              );
            }
          });
        };

        fileReader.onerror = () => {
          this.rxNotificationService.addErrorMessage(
            this.translateService.instant(
              'com.bmc.arsys.rx.innovation-studio.upload-translations.file-load-failure.message'
            )
          );
        };

        fileReader.readAsText(file);
        element.value = '';
      }

      unlisten();
    });

    this.renderer.setAttribute(this.fileInput.nativeElement, 'accept', '.json');
    this.renderer.selectRootElement(this.fileInput.nativeElement, true).click();
  }

